<template>
  <div class="search-result">
    <v-scroll class="suggest" :data="result" :pullup="pullup" :beforeScroll="beforeScroll" @beforeScroll="listScroll" @scrollToEnd="searchMore"　ref="suggest">
      <ul class="suggest-list">
        <li class="suggest-item" v-for="item in result" v-if="item.subtype !== 'tv'" @click="selectItem(item)">
          <div class="item-pic">
            <img :src="item.image" width="80" height="100">
          </div>
          <div class="item-info">
            <div class="title">{{item.title}}</div>
            <div class="info">
            	<span class="ratings">豆瓣评分&nbsp<b>{{getRating(item.rating)}}</b></span>
              <span class="derector">导演：{{item.director}}</span>
              <span class="casts">演员：{{item.casts}}</span>
              <span class="pubdate">{{getPubdate(item.pubdates)}}</span>
            </div>
          </div>
        </li>
        <v-loadmore :hasMore="hasMore" v-if="result.length"></v-loadmore>
      </ul>
      <div class="no-result" v-if="noResult">无搜索结果，请重新搜索！</div>
      <v-loading :fullScreen="fullScreen" v-if="!result.length && !noResult"></v-loading>
    </v-scroll>
  </div>
</template>

<script type="text/ecmascript-6">
  import VScroll from '@/baseparts/scroll/scroll';
  import VLoadmore from '@/baseparts/loadmore/loadmore';
  import VLoading from '@/baseparts/loading/loading';
  import { movieSearch, tagSearch } from '@/api/movie-search';
  import { createSearchList } from '@/common/js/movieList';
  import { mapMutations } from 'vuex';
  const SEARCH_MORE = 20;
  export default {
    props: {
      query: {
        type: String,
        default: ''
      },
      tagSearch: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        searchIndex: 0,
        result: [],
        pullup: true,
        hasMore: true,
        beforeScroll: true,
        loadingFlag: true,
        fullScreen: true,
        dataFn: null,
        noResult: false
      };
    },
    created() {
      this._selectType();
    },
    methods: {
      search() {
        // 首先需要重置一些状态
        this.result = [];
        this.hasMore = true;
        this.loadingFlag = true;
        this.searchIndex = 0; // 变更搜索内容时重置
        this.$refs.suggest.scrollTo(0, 0); // 回滚到顶部
        this.dataFn(this.query, this.searchIndex, SEARCH_MORE).then((res) => {
          const queryRet = res.title.split('"');
          if (this.query !== queryRet[1]) { // 防止由于加载缓慢，之前废弃的数据篡改当前数据
            console.log('old request + 1', queryRet[1]);
            return; // 舍弃过时的请求
          }
          if (!res.subjects.length) { // 判断是否存在搜索结果
            this.noResult = true;
          } else {
            this.noResult = false;
          }
          this.result = createSearchList(res.subjects);
          console.log(this.result);
          this._checkMore(res);
          // console.log(this.result);
        });
      },
      searchMore() {
        if (!this.loadingFlag) { // 上一次加载还未完成时候，忽略此次事件
          return;
        }
        this.loadingFlag = false;
        if (!this.hasMore) {
          return;
        }
        this.searchIndex += SEARCH_MORE;
//      console.log('need more');
        this.dataFn(this.query, this.searchIndex, SEARCH_MORE).then((res) => {
          this.result = this.result.concat(createSearchList(res.subjects));
          this._checkMore(res);
          this.loadingFlag = true;
        });
      },
      getPubdate(date) {
        let pubdate = '';
        for (let i = 0; i < date.length; i++) {
          if (date[i].indexOf('电影节') === -1) {
            pubdate = date[i]; // 没有在中国大陆上映，取不为电影节的最后一个信息
          }
          if (date[i].indexOf('中国大陆') > -1) {
            pubdate = date[i];
            break;
          }
        }
        if (!pubdate) {
          return '';
        }
        return pubdate;
      },
      getRating(rate) {
        if (rate === 0) {
          return '暂无评分';
        }
        return rate + '分';
      },
      selectItem(item) {
        // 点击搜索结果跳转
        this.$router.push({
          path: `/movie/${item.id}`
        });
        this.setMovie(item);
        this.$emit('select'); // 派发事件用于更改搜索历史
      },
      listScroll() { // 移动端搜索时，键盘防遮挡
        this.$emit('listScroll');
      },
      _checkMore(data) {
        let movies = data.subjects;
        if (!movies.length || data.start + data.count >= data.total) {
          this.hasMore = false;
        }
      },
      _selectType() {
        this.dataFn = this.tagSearch ? tagSearch : movieSearch;
      },
      ...mapMutations({
        'setMovie': 'SET_MOVIE'
      })
    },
    watch: {
      query() {
        this._selectType();
        if (this.query) { // 搜索框有数据
          this.result = []; // 清空数据
          this.search(); // 获得检索数据
        }
      }
    },
    components: {
      VScroll,
      VLoading,
      VLoadmore
    }
  };
</script>

<style lang="scss" scoped>
  @import "../../common/style/variable.scss";
  
  
  .suggest {
  	height: 100%;
  	overflow: hidden;
  	.suggest-list {
  		padding: 15px;
  		.suggest-item {
  			margin-top: 10px;
  			display: flex;
  			border-bottom:1px solid #eee;
  			&:first-child {
  				margin-top: 0;
  			}
  			.item-pic {
  				flex: 80px 0 0;
  				width: 50px;
  				margin-right: 10px;
  			}
  			.item-info {
  				flex: 1;
  				padding-bottom: 15px;
  				.title {
  					font-size: $font-size-medium;
  					color: $color-text-f;
  					margin-bottom: 10px;
  				}
  				.info {
  					font-size: 0;
  					span {
  						font-size: 12px;
  						display: block;
  						margin-bottom:10px;
  					}
  					.ratings{
  						font-size: 14px;
  						b{
  							font-size: 15px;
  						}
  					}
  				}
  			}
  		}
  	}
  	.no-result {
  		position: absolute;
  		top: 50%;
  		width: 100%;
  		text-align: center;
  	}
  }
  
  </style>


