<template>
  <div class="movie-comment">
    <div class="type-title" v-if="needTitle">影评{{reviewNum}}条
    </div>
    <ul>
      <li v-for="review in reviews" class="review-item" @click="selectReview(review.id)">
        <div class="content">
          <h1 class="title">{{review.title}}</h1>
          <div class="user-info">
            <div class="avatar">
              <img v-lazy="review.author.avatar" alt="" width="25" height="25">
            </div>
            <span class="name">{{review.author.name}}</span>
          </div>
          <p class="desc">{{review.summary}}</p>
        </div>
      </li>
    </ul>
    <div v-if="!needTitle" class="allReview">
      <span>全部影评{{reviewNum}}个</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      reviews: {
        types: Array,
        default: []
      },
      reviewNum: {
        type: Number,
        default: 0
      },
      needTitle: {
        type: Boolean,
        default: false
      },
      hasMore: {
        type: Boolean,
        default: true
      }
    },
    methods: {
      selectReview(id) {
        this.$emit('selectReview', id);
      }     
    }
  };
</script>

<style lang="scss" scoped>
  @import "../../common/style/variable.scss";
  
.movie-comment {
	padding: 20px;
	background: $color-background;
	.type-title {
		font-size: $font-size-medium;
		margin-bottom: 15px;
	}
	.review-item {
		margin-bottom: 20px;
		.content {
			font-size: $font-size-small;
			.title {
				font-size: $font-size-medium-x;
				color: $color-text-f;
				padding: 5px 0;
			}
			.user-info {
				padding: 5px 0;
				.avatar {
					display: inline-block;
					vertical-align: middle;
					margin-right: 6px;
					img {
						border-radius: 50%;
					}
				}
				.name {
					display: inline-block;
					vertical-align: middle;
				}
				.star {
					display: inline-block;
					vertical-align: middle;
				}
			}
			.desc {
				line-height: 20px;
			}
		}
	}
	.allReview {
		font-size: $font-size-medium;
		color: $color-yushou;
		text-align: center;
		padding-bottom: 10px;
	}
}
      
   
</style>



